/* this makes the elements be flush with the borders */
body, html { 
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  height: 100vh;
}

.Navbar {
  background-color: lightgrey;
  align-items: center;
  text-align: right;
  height: 5vh;
  min-height: 50px;
  padding: 0 0 0 0;
}

.NavbarButton {
  background-color: white;
  height: 50px;
  width: 100px;
  border-style: none;
  margin: 0px 5px 0px 5px;
  border-radius: 10px;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

.NavbarButton:hover {
  background-color: bisque;
}

.NavbarButton:active {
  background-color: grey;
}

.Board {
  border: 1px;
  display: flex;
  align-items: center;
  align-content: center;
  /* border-color: red; */
  /* border-style: solid; */
}

.BoardNote {
  text-align: center;
  vertical-align: middle;
  display: flex;
  align-items: center;
  background-color: rgb(157, 157, 157);
  border-radius: 5px;
  width: 100px;
  height: 100px;
  position: absolute;

  padding-left: 10px;
}

.BoardNote:hover {
  background-color: lightgrey;
}
